<template>
	<view class="container" style="position: relative;" :class="windowHeight ? 'stt' : ''">
		<TekyGoTop></TekyGoTop>
		<popUp ref="index"></popUp>
		<view class="" v-show="indexFlag" style="background-color: #ea5c2c;">
			<view class="" style="height: 18rpx; "></view>
			<view class="" style="margin: 0 55rpx 117rpx;">
				<image src="../../static/Index/index-1.png"  mode="widthFix" 
					style="width: 640rpx; border-radius: 10rpx;"></image> 
			</view>  
			<!--  --> 
			<view class=" bgc-o-y b-b" style=" position: relative; width: 750rpx;">
				<view class="dingwei2 df-jcc-aic" style="width: 750rpx;">
					<view class="df-jcc-aic" >
						<view class="b-r-15 df-jcc-aic index1 " hover-class="cartoon"
							style="background-color: #2e92ce;" @click="sTrampoline">  
							<image src="../../static/img/Trampoline.png" mode="widthFix" 
								style="width: 117rpx; border-radius: 50%;"></image>
							<view  class="ta-c FFF fw800 f27rpx">{{Tname}}</view>
						</view>  
						<view v-if="ifSque" class="b-r-15 df-jcc-aic index1 " hover-class="cartoon"
							@click="sSqueezies"
							style="margin-left: 36rpx; background-color: #9d60a2;">
							<image class="b-r-50" src="../../static/img/squeeze.png" mode="widthFix" 
								style="width: 117rpx;"></image>
							<view  class="ta-c FFF fw800 f27rpx">{{Sname}}</view>
						</view>  
					</view>
 
				</view> 
				<view class="" style="height: 10rpx;"></view>
				<view class="" style="">
					
					<scroll-view scroll-y="true" style="height: calc(100vh - 583rpx);">
						<view class="" v-show="flag">
							<view class="" style="height: 90rpx;"></view>
							<view class="kaPian bgc_fa" v-for="(item,index) in TramData"
							hover-class="hover_FFF" hover-start-time=0 hover-stay-time=150
							:key="index" @click="goGameDetail(item)">	
								<view  class="df-jcsb" style="height: 100%; align-items: center;">
									<view class="" style="width: 200rpx;">
										<view class="df-jcc-aic ta-c">
											<image :src="item.iconUrl" 
											style="width: 150rpx; height: 130rpx;"></image> 
										</view>
									</view> 
									<view class="ta-c" style=""> 
										<view class="f30rpx fw800" style="color: #044a80; white-space: nowrap">{{item.title}}</view>
									</view> 
									<view  class="df" style="width: 140rpx; align-items: center;">
										<view class="df-jcc-aic b-r-50 box-sha" style="height: 71rpx; width: 71rpx;">
											<image class="" src="../../static/Index/sjx.png" mode="widthFix" style="width: 30rpx;"></image>
										</view>
									</view>
								</view>
							</view>
						</view>
						
						<view class="" v-show="!flag">
							<view class="" style="height: 90rpx;"></view>
							<view class="kaPian bgc_fa" v-for="(item1,index1) in SqueData"
							:key="index1" @click="goGameDetail(item1)">	
								<view  class="df-jcsb" style="height: 100%; align-items: center;">
									<view class="" style="width: 200rpx;">
										<view class="df-jcc-aic">
											<image :src="item1.iconUrl" 
											style="width: 150rpx; height: 130rpx;"></image> 
										</view>
									</view>  
									<view class="ta-c" style=""> 
										<view class="f30rpx fw800" style="color: #044a80;">{{item1.title}}</view>
									</view> 
									<view  class="df" style="width: 140rpx; align-items: center;">
										<view class="df-jcc-aic b-r-50 box-sha" style="height: 71rpx; width: 71rpx;">
											<image class="" src="../../static/Index/sjx.png" mode="widthFix" style="width: 30rpx;"></image>
										</view>
									</view>
								</view>
							</view>
						</view>
						<view class="" style="height: 50rpx;"></view>
					</scroll-view> 
				</view>
			</view> 
		</view>
		<view class="" style="" v-if="gamesDetailFlag" >
			<gamesDetail ref="videoStop" @forGameDetail="forGameDetail" :dates="dates" :day="days"></gamesDetail>
		</view>
		<view v-if="gamesDetailFlag" @click="fanHuiss" class="" 
			style="position: absolute; left: 50rpx; top: 200rpx;"
			hover-class="hover1" hover-start-time=0 hover-stay-time=150 >
			<image src="../../static/Index/fanHui.png" mode="widthFix" style="width: 25rpx;"></image>
		</view>
	</view> 
</template>

<script>
	import gamesDetail from './gameDetails.vue'
	export default {
		components:{gamesDetail},
		data() {
			return {
				indexFlag:true,
				gamesDetailFlag:false,
				TramDataFlag:false,
				flag:true,
				windowHeight:false,
				ifSque:false,
				Sque:true, 
				data:10,
				height:0,
				flags:1,
				Tram_id:'',
				Sque_id:'',
				TramData:[], 
				SqueData:[],
				Data:[],
				dates:{},
				day:'',
				days:'',
				Tname:'',
				Sname:'',
			}
		},
		created() {
			// console.log('created');
			this.getList()
			this.sTrampoline()
			// this.sSqueezies()
		},
		mounted() {
			let that = this
			uni.getSystemInfo({
				success(res) {
					if(res.windowHeight >700){
						that.windowHeight = true
					}else{
						that.windowHeight = false
					}
				}
			})

		},

		onShow() {
			let _this = this
			uni.getNetworkType({
					success: function (res) {
							console.log(res.networkType);
							if(res.networkType == 'none'){
								// console.log('无网络');
								_this.flags = 2
							}else{
								// console.log('有网络');
								_this.flags = 1
							}
					}
			});
			this.$nextTick(function(){
				if(this.flags == 2){
					this.$refs.index.openPopup('Request out of time, please check network and try it again')
				}else{
					
				}
			})
		}, 
		onHide() {
			this.gamesDetailFlag = false 
			this.indexFlag = true
		},
		methods: {
			
			fanHuiss(){
				console.log('fanhui');
				this.gamesDetailFlag = false
				this.indexFlag = true
			}, 
			//获取游戏目录
			async getList(){
				let _this = this
				const res = await this.$myRequest({
					url:'/api/category/list',
				})    
				if(res.data.succeeded){
					this.Tname = res.data.data.records[0].name
					this.Sname = res.data.data.records[1].name
					_this.Tram_id = res.data.data.records[0].id
					_this.Sque_id = res.data.data.records[1].id
				}
			}, 
			// 获取蹦床数据
			async sTrampoline(){
				this.TramData = []
				this.flag = true
				const res1 = await this.$myRequest({
					url:'/api/game/list'+'?categoryId='+ this.Tram_id +'&page=1'+'&limit=-1',
				}) 
				if(res1.data.succeeded){
					this.TramData = res1.data.data.records
				}
			}, 
			// 获取健身球数据
			async sSqueezies(){
				let _this = this
				this.flag = false
				// this.SqueData = []
				const res2 = await this.$myRequest({
					url:'/api/game/list'+'?categoryId='+this.Sque_id+'&page=1'+'&limit=-1',
				}) 
				if(res2.data.succeeded){
					if(res2.data.data.records.length == 0){
						// console.log('无健身球');
						_this.ifSque = false 
					}else{
						// console.log('有数据');
						_this.ifSque = true
						_this.SqueData = res2.data.data.records
					}
				}
			},
			goGameDetail(e){
				this.dates = e
				console.log(e);
				this.day = e.createTime
				console.log('day='+this.day);
				let year = this.day.slice(0,4)
				let month = this.day.slice(5,7)
				let day = this.day.slice(8,10)
				this.days = day + '/'+month+'/'+year
				// console.log(this.days);
				this.indexFlag = false
				this.gamesDetailFlag = true
			},
			forGameDetail(){
				this.indexFlag = true
				this.gamesDetailFlag = false
			},

			
		}
	}
</script>

<style>
	/* page{ background-color: #ea5c2c; } */
	.b-b{
		border-top-right-radius: 60rpx; 
		border-top-left-radius: 60rpx;
	}
	.dingwei2{
		position: absolute; 
		left: 0rpx;
/* 		left: 167.5rpx; */
		top: -104rpx;
	}
	.kaPian{
		box-shadow: #000 .2rpx .2rpx 0;
		height: 144rpx; 
	
		border-radius: 30rpx;
		margin: 10rpx 38rpx;
	}
	.index1{
		width: 189rpx;
		height: 198rpx; 
		flex-direction: column;
		z-index: 1;
	}

</style> 
